@media (min-width: 100px) and (max-width: 500px) {
  .main_nav {
    flex-direction: column;
  }
  .nav_logo {
    width: 40%;
    margin: 1em 0;
  }
  .home {
    width: 95%;
  }
  .nav_links {
    flex-direction: column;
    min-width: 200px;
  }
  .nav_links button {
    margin: 0.56em 0;
  }
  .home {
    flex-direction: column;
  }
  .left {
    width: 100%;
    margin-left: 0.5em;
  }
  .title_home {
    font-size: 2rem;
    width: 100%;
    line-height: 43px;
  }
  .p-home {
    width: 93%;
    margin-top: 12px;
  }
  .info {
    width: 90%;
    left: 0;
    min-height: 100px;
    top: 340px;
    padding: 0;
  }
  .main_btn {
    position: relative;
    width: 20%;
  }
  .home_btn {
    position: absolute;
    top: 45px;
    display: block;
    padding: 2px 10px;
    width: 70px;
    margin: 0 auto;
    left: 30px;
  }
  .right {
    width: 70%;
  }
  .right img {
    width: 100%;
  }
  .reasons {
    margin-top: 200px;
  }
  header {
    width: 100%;
    text-align: center;
    flex-direction: column;
  }
  .reason-title {
    font-size: 1.5rem;
    width: 100%;
    margin-bottom: 17px;
    line-height: 33px;
  }
  .reason_price {
    widows: 100%;
  }
  .reason_cards {
    flex-direction: column;
  }
  .reason_block {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .reason_block .title {
    font-size: 1.2rem;
  }
  .proof_card {
    width: 100%;
    margin-top: 2.4em;
    flex-direction: column;
    align-items: center;
  }
  .proof_card img {
    width: 40%;
  }

  .textes .tit {
    margin: 12px 0;
  }
  .tit {
    text-align: center;
    font-weight: 700;
  }
  .furni_title {
    font-size: 1rem;
  }
  .furni_descr {
    font-size: 1.8rem;
    line-height: 33px;
    width: 90%;
  }
  .firni_collection {
    flex-direction: column;
  }
  .furni_card {
    width: 100%;
  }
  .furni_card p {
    width: 100%;
  }
  .review h3 {
    font-size: 1.4rem;
    width: 100%;
  }
  .review_blocks {
    flex-direction: column;
  }
  .review_descr {
    width: 100%;
  }
  .partners {
    flex-wrap: wrap;
    width: 100%;
  }
  .partners img {
    width: 40%;
    margin: 1em 0;
  }
  .photo_titles {
    width: 100%;
  }
  .photo_titles .title {
    width: 100%;
  }
  .city_collection {
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
  }
  .city_btn {
    padding: 6px 12px;
  }
  .city_block {
    margin: 0.78em;
  }
  .footer_up {
    flex-wrap: wrap;
  }
  .foo {
    margin: 1em;
  }
  .footer_descr {
    width: 100%;
  }
  .contacts img {
    width: 20px;
  }
  .contacts span {
    font-size: 0.78rem;
  }
  .foot_images {
    display: flex;
    margin-top: 0.55em;
  }
  .contacts {
    display: flex;
    flex-direction: column;
  }
  .contacts img {
    display: flex;
  }
  .rights {
    font-size: 0.89rem;
    width: 45%;
  }
}
@media (min-width: 501px) and (max-width: 850px) {
  .furni_title {
    font-size: 1rem;
  }
  .furni_descr {
    font-size: 1.8rem;
    line-height: 33px;
    width: 90%;
  }
  .firni_collection {
    flex-direction: column;
    align-items: center;
  }
  .furni_card {
    width: 70%;
  }
  .furni_card p {
    width: 100%;
  }
  .left {
    width: 80%;
  }
  .info {
    width: 90%;

    min-height: 100px;
    top: 390px;
    padding: 0;
    margin: 0 auto;
  }
  .main_btn {
    position: relative;
    width: 20%;
  }
  .home_btn {
    position: absolute;
    top: 45px;
    display: block;
    padding: 2px 10px;
    width: 70px;
    margin: 0 auto;
    left: 30px;
  }
  .reasons {
    margin-top: 167px;
  }
  .reason-title .reason_price {
    margin-top: 165px;
  }
  .reason-title {
    font-size: 1.9rem;
    width: 100%;
  }
}
