.places {
  max-width: 100%;
  color: white;
  background: #1b1918;
  padding: 50px 20px;
  margin-top: 60px;
}
.photo_titles .title {
  font-size: 1.5rem;
  font-weight: normal;
}

.city_collection {
  display: flex;
  max-width: 95%;
  justify-content: space-between;
}
.city_name {
  margin-bottom: -4px;
}
.city_state {
  color: #a7a6a4;
}
.city_block {
  margin-top: 23px;
}
.city_title {
  font-weight: 500;
  font-size: 1.3rem;
}
.city_btn {
  background: #2e2b29;
  border-radius: 8px;
  border: none;
  text-align: center;
  color: white;
  padding: 12px 25px;
  margin-top: 30px;
  text-transform: capitalize;
}
.city_btn:hover {
  cursor: pointer;
  background: #ffffff40;
}
#photo-img {
  width: 25%;
  object-fit: contain;
  margin: 0 auto;
  min-width: 60%;
}
