footer {
  margin-top: 40px;
  max-width: 100%;
  background: #194240;
  padding: 40px 20px;
  color: white;
}
.footer_up {
  display: flex;
  justify-content: space-between;
}
.footer_descr {
  width: 35%;
}
.foo div {
  color: rgba(255, 255, 255, 0.6);
}
.foo p {
  margin-bottom: 12px;
}
.footer_up {
  border-bottom: 1px solid #e5e7eb;
  padding-bottom: 1.4em;
}
.footer_down {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  max-width: 100%;
}
.rights {
  color: rgba(255, 255, 255, 0.3);
}

.span_number {
  display: inline-block;
  margin-right: 1em;
}
.contacts img {
  display: inline-block;
  margin: 0 0.25em;
}
.contacts {
  display: flex;
}
