.review {
  padding: 60px 20px;
}
.review h3 {
  margin-left: .50em;
  width: 45%;
  font-size: 2.5rem;
}
.review_descr {
  font-size: 0.89rem;
  width: 90%;
}
.address {
  margin-top: -10px;
  color: rgba(0, 0, 0, 0.6);
}
.review_blocks {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.review_block {
  border: 1px solid #cecdca;
  border-radius: 8px;
  padding: 10px 15px;
  margin: 0.9em;
}
