.proof {
  max-width: 100%;
  width: 100%;
  background: #fbede4;
  display: flex;
  justify-content: space-between;
}
.left {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  justify-content: center;
}
.title {
  font-size: 1.5rem;
  font-weight: 700;
  width: 90%;
}
.card img {
  width: 20%;
  min-width: 52px;
  object-fit: cover;
}
.proof_card {
  margin: 1em 0;
  display: flex;
  justify-items: space-between;
}
.textes {
  margin: 0 1em;
  width: 100%;
}
.textes p {
  min-width: 200px;
  margin-bottom: -10px;
  max-width: 392px;
}
.descr {
  width: 80%;
}
.proof_img {
  width: 100%;
  max-width: 640px;
}
