.main {
  max-width: 100%;
  color: white;
  background: #194240;
  padding: 80px 37px;
  position: relative;
}
.furni_title {
  font-size: 0.85rem;
  font-weight: 400;
  margin-left: 0.4em;
}
.carousel .slide img{
  width:60%
}
.furni_descr {
  margin-bottom: 2em;
  font-size: 3rem;
  width: 59%;
  font-weight: normal;
  margin-top: 0.9em;
  line-height: 59px;
}
.firni_collection {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  justify-content: center;
}
.furni_card {
  width: 22%;
  margin: 0.45em;
}
.furni_card p {
  font-size: 0.9rem;
  width: 100%;
  text-align: center;
}
.furni_card img {
  width: 90%;
  min-width: 191px;
}

.bottomPic {
  position: absolute;
  bottom: 0;
}
.car_img{
  width: 60%;
  object-fit: cover;
}