body{
  font-family: Roboto;
}

.main_nav{
  display:flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 20px;
}
.nav_links{
  display:flex;
  justify-items: space-between;
  list-style:none;
}
.main_nav li{
  margin: 1em;
  font-weight: 700; 
}
.nav_links button{
  background: rgba(0, 0, 0, 0.06);
border-radius: 8px;
text-align:center;
border:none;
color: #000000;
font-size: .95rem;
padding: 6px 15px;  
}
.nav_logo img{
width: 90%;
min-width: 120px;
}
.home{
  display:flex;
  position:relative;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #F3F3F2;
  margin-top: 20px;
}
.left{
  position: relative;
  display:flex;
  flex-direction:column;
  align-items: center;
  width:50%;
  text-align:left;
 padding: 10px 0px;
}
.title_home{
  width: 50%;
 font-size: 2rem;
 font-weight:700;

}
.p-home{
  color: #55524E;
  width: 50%;
}
.info{
  position: absolute;
  z-index: 100;
  top: 200px;
  margin-top: 3.5em;
  width:100%;
  padding: 0px 20px;
  left: 100px;
  display:flex;
  background: #FFFFFF;
box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.12);
border-radius: 8px;
min-height: 60px;

}
.info button{
    /* margin-top: 12px; */
    margin: 13px 10px 12px 15px;
    background: #08786C;
    border-radius: 8px;
    border: none;
    color: white;
    text-align: center;
    padding: -2px 13px;
    display: block;
    width: 18%;
    min-width: 123px;
    padding: 17px 30px;
 
}
.second{
  width:40%;
}

.info_col{
  margin: 1em;
}
.loc{
  margin-bottom: .45rem;
  color: #787573;
}
.info_col:first-child{
  width: 45%;
border-right: 2px solid  #CECDCA
}


.right{
  width:50%;
  padding: 0 0px;
 
}
.right img{
  width:100%;
  max-width: 490px;
  display:block;
  max-height: 660px;
  object-position: cover

}

/* second section */
.reasons{
  margin: 3.4em .45em;
  padding: 0 20px;
}
.reason-title{
  font-size: 2.3rem;
  width: 35%;
  text-align:left;
}
.reasons header{
  display:flex;
  justify-content:space-between;
  align-items: center;
  padding: 0 23px;
}
.reason_price{
  color: #787573;
  font-size: 1rem;
}
.learn_more{
  font-size:.95rem;
  border:none;
  background: transparent;
  margin: 2rem 0;
  font-weight: 700;

  padding:0 23px;
}
.reason_cards .title{
  font-size: 1.3rem;
  font-weight: 700
}
.reason_block{
  width: 30%;
  margin: .55em;
}
.reason_cards .text{
  color: #55524E;
  font-size: 1rem;
  margin-top:12px;
  width: 88%
}
.reason_cards{
  display:flex;
  flex-wrap:wrap;
  justify-content: center;
}
.reason_block img{
  width: 20%;
  min-width:60px;
}